import { React, useEffect, useState } from 'react'
import styled from 'styled-components'
import { AutoColumn } from '../Column'
import NumericInput from '../NumericInput'
import { RowBetween } from '../Row'
import { TokenListProvider } from '@solana/spl-token-registry';
import { Modal, Image, DropdownButton } from 'react-bootstrap'
import { getChainId } from '../../utils/mics'
import { network } from '../../utils/constants'
import {
    getTokenBalance,
    getTokenAccounts,
    getAssociatedTokenAddress,
} from '../../utils/api'
const defaultURI = "https://cryptologos.cc/logos/solana-sol-logo.png";

const LabelRow = styled.div`
  display: flex;
  flex-flow: row-reverse wrap;
  align-items: center;
  span:hover {
    cursor: pointer;
    color: gray;
  }
`

export default function AmountInputPanel({
    connection,
    account,
    value,
    onChangeAmount,
    onSelectToken,
    onTokenBalance,
    onSetShowCreateAccount,
    onError,
    updateBalance,
    updatedAddress,
    setBalanceUpdated,
}) {
    const [allTokens, setAllTokens] = useState([
        {
            decimals: 9,
            symbol: "MetaSPL",
            address: "Cv7DvSt9842MSxX8LXQewpfcWNC8qT5P9w7wpHshcEzx",
        }
    ]);

    const [openSelectTokenModal, setOpenSelectTokenModal] = useState(false);
    const [selectedCurrencyBalance, setSelectedCurrencyBalance] = useState("");
    const [selectedTokenSymbol, setSelectedTokenSymbol] = useState("Select a token");

    const handleSelectToken = async (event) => {
        handleModalClose();

        const data = allTokens.filter((element) => {
            return element.address === event
        });
        const tokenInfo = data[0];

        onSelectToken(data[0]);
        setSelectedTokenSymbol(data[0].symbol);

        if (account) {
            const addresses = await getTokenAccounts(
                connection, tokenInfo.address, account
            );

            if (addresses && addresses.length > 0) {
                const tokenAccount = await getAssociatedTokenAddress(
                    tokenInfo.address, account
                );

                const balance = await getTokenBalance(
                    connection, tokenAccount
                );

                setSelectedCurrencyBalance(balance.value.uiAmount);
                onTokenBalance(balance.value.uiAmount);
            } else {
                onSetShowCreateAccount();
                onError("You don't have token account for this token.")
                setSelectedCurrencyBalance("");
            }
        }
    }

    const fetchTokenList = () => {
        const chainId = getChainId(network);
        new TokenListProvider().resolve().then(async (tokens) => {
            const tokenList = tokens.filterByChainId(chainId).getList();
            setAllTokens(allTokens.concat(tokenList));
        });
    }

    const onClickMax = () => {
        onChangeAmount(selectedCurrencyBalance);
    }

    const handleModalClose = () => {
        setOpenSelectTokenModal(false);
    }

    useEffect(() => {
        if (allTokens.length === 1) {
            fetchTokenList();
        }

        if (updateBalance) {
            handleSelectToken(updatedAddress);
            setBalanceUpdated();
        }
        // eslint-disable-next-line
    }, [updateBalance]);

    return (
        <div id="mainInput">
            <div className="input-container">
                <AutoColumn gap="md">
                    <LabelRow>
                        <RowBetween>
                            <div
                                fontWeight={500}
                                fontSize={14}
                            >
                                Amount
                                </div>

                            {account && (
                                <div
                                    onClick={onClickMax}
                                    color="rgb(86, 90, 105)"
                                    fontWeight={500}
                                    fontSize={14}
                                    style={{ display: 'inline', cursor: 'pointer' }}
                                >
                                    {selectedCurrencyBalance !== ""
                                        ? 'Balance: ' + selectedCurrencyBalance.toFixed(3)
                                        : ' -'
                                    }
                                </div>
                            )}
                        </RowBetween>
                    </LabelRow>

                    <LabelRow>
                        <RowBetween>
                            <NumericInput
                                value={value}
                                onChange={onChangeAmount}
                            />

                            <DropdownButton
                                title={selectedTokenSymbol}
                                onClick={() => setOpenSelectTokenModal(true)}
                            >

                            </DropdownButton>

                            <Modal
                                show={openSelectTokenModal}
                                onHide={handleModalClose}
                                animation={false}
                                className="select-token-modal"
                            >
                                <Modal.Header closeButton>
                                    <Modal.Title>Choose Token</Modal.Title>
                                </Modal.Header>
                                <Modal.Body>
                                    {allTokens.map((element) => (
                                        element.symbol !== "SOL" ? (
                                            <div
                                                className="modal-div"
                                                key={element.address}
                                                onClick={() => handleSelectToken(element.address)}
                                            >
                                                <Image
                                                    width="20px"
                                                    src={
                                                        element.logoURI ?
                                                            element.logoURI :
                                                            defaultURI
                                                    }
                                                    style={{ marginRight: "20px" }}

                                                />
                                                <span
                                                    style={{ fontSize: "20px" }}
                                                >
                                                    {element.symbol}
                                                </span>
                                            </div>
                                        ) : null
                                    ))}
                                </Modal.Body>
                            </Modal>
                        </RowBetween>
                    </LabelRow>
                </AutoColumn>
            </div>
        </div >
    )
}